<template>
  <div class="login-page">
    <div class="main-login">
      <div class="welcome-back">
        Welcome back
      </div>
      <label class="form-box-label" :style="email.length ? 'opacity: 1; transform: translateY(0px); z-index: 2;' : ''"> Email </label>
      <input type="text" placeholder="Email" class="form-box" v-model="email" spellcheck="false"/>
      <label class="form-box-label" :style="password.length ? 'opacity: 1; transform: translateY(0px); z-index: 2;' : ''"> Password </label>
      <input type="password" placeholder="Password" class="form-box" v-model="password"/>
      <div class="wrong-login" id="wrong-login"> 
        Wrong email or password 
      </div>
      <div class="full-width">
        <button class="sign-in" @click="signIn" id="login-button" :style="email.length && password.length ? 'opacity: 1' : ''"> 
          <span id="log-in-text">Log in</span>
          <div class="loader" id="login-loader"/>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      email: '',
      password: '',
    }
  },
  mounted(){
    document.getElementsByClassName('footer')[0].style.display = 'none';
    document.getElementsByClassName('mobile-logo')[0].style.display = 'none';
    document.getElementsByClassName('navbar-link').forEach(element => {
      element.style.display = 'none';
    });
    document.getElementById('desktop-navigation-links').style.display = 'block';
    document.getElementsByClassName('navigation-drop')[0].style.display = 'none';
    document.getElementById('navbar-border').style.borderBottom = '1px solid white';
  },
  unmounted(){
    document.getElementsByClassName('footer')[0].style.display = 'flex';
    document.getElementsByClassName('mobile-logo')[0].style.display = '';
    document.getElementsByClassName('navbar-link').forEach(element => {
      element.style.display = '';
    });
    document.getElementById('desktop-navigation-links').style.display = '';
    document.getElementsByClassName('navigation-drop')[0].style.display = '';
    document.getElementById('navbar-border').style.borderBottom = '';
  },
  methods: {
    signIn: function(){
      document.getElementById('login-loader').style.display = 'block';
      document.getElementById('log-in-text').innerText = '';
      document.getElementById('wrong-login').style.opacity = 0; 

      setTimeout(() => {
        document.getElementById('login-loader').style.display = 'none';
        document.getElementById('log-in-text').innerText = 'Log in';
        document.getElementById('wrong-login').style.opacity = 1;    
      } , 2000);
    }
  }
}
</script>

<style scoped>
  .login-page{
    padding: 180px 30px;
    z-index:200;
    background: var(--white-gray);
    height: calc(100vh - 360px);
  }
  .main-login{
    text-align: left;
    width: 100%;
    max-width: 432px;
    margin: 0 auto;
  }
  .welcome-back{
    font-size: 40px;
    margin-bottom: 48px;
    font-weight: 600;
  }
  .form-box{
    border: none;
    border-bottom: 2px solid var(--lightest-gray);
    margin-bottom: 32px;
    font-size: 16px;
    font-family: inherit;
    padding: 7px 0;
    width: 100%;
    font-weight: 600;
    background: transparent;
  }
  .form-box:focus{
    outline: none;
    border-bottom: 2px solid black;
  }
  .form-box-label{
    opacity: 0;
    transition: .3s all;
    transform: translateY(20px);
    text-align: left;
    font-size: 12px;
    z-index: -2;
    color: var(--light-gray);
    width: 100%;
  }
  .wrong-login{
    width: 60%;
    border: 1px solid red;
    background: #fceae9;
    opacity: 0;
    transition: .3s all;
    padding: 10px 35px;
    border-radius: 4px;
  }
  .full-width{
    width: 100%;
    text-align:end;
    margin-top: 24px;
  }
  .sign-in{
    color: white;
    background: var(--primary-color);
    padding: 10px 35px;
    transition: .5s all;
    border-radius: 20px;
    opacity: 0;
    border: none;
    font-family: inherit;
    font-size: 16px;
    width: 115px;
  }
  .sign-in:hover{
    background: var(--primary-color-light);
  }
  .loader {
    border: 3px solid #f3f3f3; /* Light grey */
    border-top: 3px solid var(--primary-color-light);
    border-radius: 50%;
    width: 10px;
    height: 10px;
    animation: spin 1s ease-in-out infinite;
    display: none;
    margin: 0 auto;
  }
  /* Safari */
  @-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); }
  }
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }

</style>